<template>
    <div class="account">
<van-nav-bar
  title="账号管理"
  left-text="返回"
  left-arrow
  @click-left="$router.go(-1)"
/>

<van-cell-group >
 
 <van-cell title="头像" icon="user-o">
 
  
    <div class="div clearfix fr">
        <div class="fr ">
            
             <van-uploader   :after-read="uploadFile">
<img class="img_auto" :src="accountInfo.userImg" alt />
</van-uploader>
          </div>
    </div>
  
</van-cell>

 <van-cell title="手机号" icon="phone-o" :value="accountInfo.phone" >

</van-cell>
<van-cell title="用户ID"  icon="scan" :value="accountInfo.userId" />

<van-cell title="昵称"  icon="smile-o" value="" class="cell">
         <div class="clearfix">
          <div class="fr">
            <van-field v-model="accountInfo.nickName" input-align="right"
            @blur="changeUesrinfo({key: 'nickName', value: accountInfo.nickName, url: '/updateNickName'})"/>
          </div>
        </div>
    </van-cell>

 <van-cell>
        <div class="clearfix">
          <div class="fl text">简介</div>
          <div class="fr textarea">
            <textarea  v-model="accountInfo.desc" 
            @change="changeUesrinfo({key: 'desc', value: accountInfo.desc, url: '/updateDesc'})"></textarea>
          </div>
        </div>
      </van-cell>
</van-cell-group>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                accountInfo:""
            }
        },
        created() {
            this.findAccountInfo()
        },
        methods: {

            uploadFile(file) {
      

      //获取图片base64
      let serverBase64Img = file.content.replace(/^data:image\/[A-Za-z]+;base64,/, '');

      //获取图片类型
      let imgType = file.file.type.split('/')[1];

      let tokenString = localStorage.getItem("__jigng");

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });

      this.axios({
        method: "POST",
        url: "/updateAvatar",
        data: {
          appkey: this.appkey,
          tokenString,
          imgType,
          serverBase64Img,
        }
      })
        .then(result => {
          this.$toast.clear();
          

          if (result.data.code == 'H001') {
            this.userInfo.userImg = result.data.userImg;
            cnsole.log(result)
          } else {
            this.$toast(result.data.msg);
          }
         
        })
        .catch(err => {
          this.$toast.clear();
          
        })
            }
            ,
            findAccountInfo(){
                let tokenString = localStorage.getItem("__jigng");
                this.axios({
                    method:'get',
                    url:'/findAccountInfo',
                    params:{
                        appkey:this.appkey,
                        tokenString
                    }
                }).then(res=>{
                    
                    if(res.data.code=="B001"){
                        this.accountInfo=res.data.result[0]
                        
                    }
                }).catch(err=>{
                    
                })
            },
            //修改简历昵称
            changeUesrinfo(o){
                let tokenString = localStorage.getItem("__jigng")
                 this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });
               this.axios({
        method: "POST",
        url: o.url,
        data: {
          appkey: this.appkey,
          tokenString,
          [o.key]: o.value
        }
      })
        .then(result => {
          this.$toast.clear();
          
        })
        .catch(err => {
          this.$toast.clear();
          
        });

            }
        },
    }
</script>

<style lang="less" scoped>
.account{
    .van-cell{
        height: 80px;
       line-height: 50px;
    }
    .cell{
        /deep/ .van-field__control{
        margin: -10px;
    }
    }
    
   .div{
       width: 30%;
   }
   /deep/ .van-cell__left-icon {
    
    margin-top: 10px;
}
.text{
    margin-left: 20px;
}
textarea{
    width: 200px;
    height: 30px;
    // border: none;
    margin-top: 20px;
       display: block;
    width: 100%;
    height: 60px;
    padding: 0;
    margin: 0;
    border: none;
    resize: none;
    text-align: right;
}
}

</style>